import React, { useEffect, useState, useRef } from "react";
import { useParams } from "react-router-dom";
import { io } from "socket.io-client";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { Button } from "@mui/material";

function EditorPage() {
  const { id: documentId } = useParams();
  const [socket, setSocket] = useState(null);
  const [quillValue, setQuillValue] = useState("");
  const quillRef = useRef();

  // ✅ Connect socket
  useEffect(() => {
    const s = io("http://localhost:5000");
    setSocket(s);
    return () => s.disconnect();
  }, []);

  // ✅ Load document content on page load
  useEffect(() => {
    const token = localStorage.getItem("token");

    fetch(`http://localhost:5000/api/document/${documentId}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        if (data && data.content) {
          setQuillValue(data.content);
        }
      })
      .catch((err) => console.error("❌ Failed to load document:", err));
  }, [documentId]);

  // ✅ Join room
  useEffect(() => {
    if (socket && documentId) {
      socket.emit("join-document", documentId);
    }
  }, [socket, documentId]);

  // ✅ Handle incoming changes
  useEffect(() => {
    if (!socket || !quillRef.current) return;

    const quill = quillRef.current.getEditor();

    const handler = (delta) => {
      quill.updateContents(delta);
    };

    socket.on("receive-changes", handler);
    return () => socket.off("receive-changes", handler);
  }, [socket]);

  // ✅ Emit changes
  useEffect(() => {
    if (!socket || !quillRef.current) return;

    const quill = quillRef.current.getEditor();

    const handler = (delta, oldDelta, source) => {
      if (source === "user") {
        socket.emit("send-changes", { documentId, delta });
      }
    };

    quill.on("text-change", handler);
    return () => quill.off("text-change", handler);
  }, [socket]);

  // ✅ Auto-save every 3 seconds
  useEffect(() => {
    const token = localStorage.getItem("token");

    const interval = setInterval(() => {
      if (quillValue) {
        fetch(`http://localhost:5000/api/document/${documentId}`, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({ content: quillValue }),
        })
          .then((res) => res.json())
          .then((data) => console.log("💾 Auto-saved"))
          .catch((err) => console.error("Auto-save error:", err));
      }
    }, 3000);

    return () => clearInterval(interval);
  }, [documentId, quillValue]);

  return (
    <div style={{ padding: "2rem" }}>
      <Button
        variant="contained"
        color="success"
        sx={{ mb: 2 }}
        onClick={async () => {
          const token = localStorage.getItem("token");
          try {
            const res = await fetch(`http://localhost:5000/api/document/${documentId}`, {
              method: "PUT",
              headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${token}`,
              },
              body: JSON.stringify({ content: quillValue }),
            });

            if (res.ok) {
              alert("Document saved manually! 💾");
            } else {
              alert("Save failed");
            }
          } catch (err) {
            alert("Server error while saving");
          }
        }}
      >
        💾 Save Document
      </Button>

      <ReactQuill
        ref={quillRef}
        theme="snow"
        value={quillValue}
        onChange={setQuillValue}
        placeholder="Start writing your collaborative document here..."
      />
    </div>
  );
}

export default EditorPage;
